.gallery {
  @include media(767px, max-width) {
    padding-top: 20px;
    padding-bottom: 40px;
  }

  @include media(768px, min-width) {
    padding-top: 60px;
    padding-bottom: 60px;
  }

  &__list {
    @include media(768px, min-width) {
      display: flex;
      flex-wrap: wrap;
      list-style: none;
      margin-right: -32px;
      margin-bottom: -32px;
    }

    @include media(1280px, min-width) {
      margin-right: -16px;
    }
  }
}

.card {
  &:not(:last-child) {
    @include media(767px, max-width) {
      margin-bottom: 20px;
    }
  }

  @include media(768px, min-width) {
    width: calc(100% / 2 - 32px);
    margin-right: 32px;
    margin-bottom: 32px;
  }

  @include media(1280px, min-width) {
    width: calc(100% / 3 - 16px);
    margin-right: 16px;
  }

  &__img {
    width: 280px;
    height: 398px;
    border-radius: 5px;
    margin-bottom: 8px;

    @include media(768px, min-width) {
      width: 336px;
      height: 455px;
    }

    @include media(1280px, min-width) {
      margin-bottom: 12px;
      width: 394px;
      height: 574px;
    }
  }

  &__title {
    font-family: 'Roboto';
    @include font(500, 12px, 1.33, null, uppercase);
    color: #000000;

    @include media(1280px, min-width) {
      font-size: 20px;
      line-height: 1.2;
    }
  }

  &__description {
    font-family: 'Roboto';
    @include font(500, 12px, 1.33, null, null);
    display: flex;
    align-items: flex-end;
    color: #FF6B01;

    @include media(1280px, min-width) {
      font-size: 20px;
      line-height: 1.2;
    }
  }

  &__rating {
    display: block;
    width: 36px;
    height: 16px;
    background-color: #FF6B01;
    @include centeredFlex;
    font-family: 'Roboto';
    @include font(500, 12px, 1.16, null, null);
    text-align: center;
    margin-left: 8px;
    color: #FFFFFF;
    border-radius: 5px;

    @include media(1280px, min-width) {
      width: 40px;
      height: 20px;
      line-height: 1.16;
    }
  }
}